html,
body,
#root {
  width: 100%;
  height: 100%;
  overflow: hidden;
  padding: 0;
  margin: 0;
  background-color: #2f3437;
}

html,
body,
* {
  font-family: "Roboto", sans-serif !important;
}

.custom-icon {
  margin-left: 5px;
  display: flex;
  flex-direction: column-reverse;
}

.custom-icon.copy-url {
  margin-left: 2px;
}

.custom-icon img {
  display: block;
  margin-bottom: 1px;
}

.chart {
  border-radius: 10px;
  overflow: hidden;
}
